"use client";

import { useState, useEffect } from 'react';
import Link from 'next/link';
import Image from 'next/image';
import { FiArrowLeft, FiBarChart2 } from 'react-icons/fi';

interface ModelMetrics {
  algorithm: string;
  accuracy: number;
  precision: number;
  recall: number;
  f1_score: number;
}

export default function ModelsPage() {
  const [metrics, setMetrics] = useState<ModelMetrics[]>([]);
  const [loading, setLoading] = useState<boolean>(true);
  const [error, setError] = useState<string | null>(null);
  const [comparisonImage, setComparisonImage] = useState<string | null>(null);
  const [featureImportanceImage, setFeatureImportanceImage] = useState<string | null>(null);

  useEffect(() => {
    const fetchModelMetrics = async () => {
      try {
        const response = await fetch('http://localhost:8080/model-metrics/');
        if (!response.ok) {
          throw new Error('Failed to fetch model metrics');
        }
        const data = await response.json();
        setMetrics(data);
        
        // Fetch model comparison visualization
        const comparisonResponse = await fetch('http://localhost:8080/visualizations/model_comparison');
        if (comparisonResponse.ok) {
          const comparisonData = await comparisonResponse.json();
          setComparisonImage(comparisonData.data.image);
        }
        
        // Fetch feature importance visualization
        const importanceResponse = await fetch('http://localhost:8080/visualizations/feature_importance');
        if (importanceResponse.ok) {
          const importanceData = await importanceResponse.json();
          setFeatureImportanceImage(importanceData.data.image);
        }
      } catch (err) {
        setError('Error fetching model metrics. Please make sure the backend server is running.');
        console.error(err);
      } finally {
        setLoading(false);
      }
    };

    fetchModelMetrics();
  }, []);

  const getBestModel = () => {
    if (metrics.length === 0) return null;
    return metrics.reduce((best, current) => 
      current.f1_score > best.f1_score ? current : best
    );
  };

  const bestModel = getBestModel();

  return (
    <div className="min-h-screen bg-gray-50 py-12 px-4 sm:px-6 lg:px-8">
      <div className="max-w-6xl mx-auto">
        <div className="flex items-center mb-8">
          <Link href="/" className="flex items-center text-purple-600 hover:text-purple-800">
            <FiArrowLeft className="mr-2" /> Back to Dashboard
          </Link>
        </div>
        
        <div className="bg-white shadow-md rounded-lg overflow-hidden">
          <div className="px-6 py-4 bg-purple-700 text-white">
            <h1 className="text-xl font-semibold">Model Comparison and Evaluation</h1>
          </div>

          <div className="p-6">
            {loading ? (
              <div className="flex justify-center items-center h-64">
                <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-purple-500"></div>
              </div>
            ) : error ? (
              <div className="p-6 bg-red-50 border border-red-200 rounded-md">
                <div className="text-red-700">
                  <p>{error}</p>
                </div>
              </div>
            ) : (
              <div className="space-y-8">
                <div>
                  <h2 className="text-xl font-semibold mb-4 flex items-center text-gray-800">
                    <FiBarChart2 className="mr-2" /> Evaluation Metrics
                  </h2>
                  
                  <div className="overflow-x-auto">
                    <table className="min-w-full bg-white">
                      <thead>
                        <tr className="bg-gray-100">
                          <th className="py-3 px-4 text-left font-semibold">Algorithm</th>
                          <th className="py-3 px-4 text-left font-semibold">Accuracy</th>
                          <th className="py-3 px-4 text-left font-semibold">Precision</th>
                          <th className="py-3 px-4 text-left font-semibold">Recall</th>
                          <th className="py-3 px-4 text-left font-semibold">F1 Score</th>
                        </tr>
                      </thead>
                      <tbody>
                        {metrics.map((model, index) => (
                          <tr 
                            key={model.algorithm}
                            className={`${index % 2 === 0 ? 'bg-white' : 'bg-gray-50'} ${
                              bestModel && model.algorithm === bestModel.algorithm ? 'bg-green-50' : ''
                            }`}
                          >
                            <td className="py-3 px-4 border-b">
                              {model.algorithm}
                              {bestModel && model.algorithm === bestModel.algorithm && (
                                <span className="ml-2 text-xs bg-green-200 text-green-800 px-2 py-1 rounded-full">Best Model</span>
                              )}
                            </td>
                            <td className="py-3 px-4 border-b">{(model.accuracy * 100).toFixed(2)}%</td>
                            <td className="py-3 px-4 border-b">{(model.precision * 100).toFixed(2)}%</td>
                            <td className="py-3 px-4 border-b">{(model.recall * 100).toFixed(2)}%</td>
                            <td className="py-3 px-4 border-b">{(model.f1_score * 100).toFixed(2)}%</td>
                          </tr>
                        ))}
                      </tbody>
                    </table>
                  </div>
                </div>

                {comparisonImage && (
                  <div>
                    <h2 className="text-xl font-semibold mb-4 text-gray-800">Models Performance Comparison</h2>
                    <div className="bg-white p-4 border border-gray-200 rounded-lg">
                      <Image 
                        src={`data:image/png;base64,${comparisonImage}`} 
                        alt="Model Comparison Visualization" 
                        className="mx-auto"
                        width={800}
                        height={600}
                      />
                    </div>
                  </div>
                )}

                {featureImportanceImage && (
                  <div>
                    <h2 className="text-xl font-semibold mb-4 text-gray-800">Feature Importance</h2>
                    <div className="bg-white p-4 border border-gray-200 rounded-lg">
                      <Image 
                        src={`data:image/png;base64,${featureImportanceImage}`} 
                        alt="Feature Importance Visualization" 
                        className="mx-auto"
                        width={800}
                        height={600}
                      />
                    </div>
                  </div>
                )}

                {bestModel && (
                  <div className="bg-green-50 p-6 rounded-lg border border-green-200">
                    <h3 className="text-lg font-semibold text-green-800 mb-2">
                      Model Recommendation
                    </h3>
                    <p className="text-gray-700">
                      Based on the evaluation metrics, the <span className="font-semibold">{bestModel.algorithm}</span> model 
                      performs best with an F1 score of {(bestModel.f1_score * 100).toFixed(2)}%.
                    </p>
                    <p className="mt-3 text-gray-700">
                      This model balances precision ({(bestModel.precision * 100).toFixed(2)}%) and 
                      recall ({(bestModel.recall * 100).toFixed(2)}%) well, making it optimal for apartment rental classification.
                      The F1 score is used as the main metric because it provides a balance between precision and recall.
                    </p>
                  </div>
                )}
              </div>
            )}
          </div>
        </div>
      </div>
    </div>
  );
}